<template>
	<div class="body">
		<div class="search_group">
			<div class="search_group_top">
				<div class="search_box">
					<el-select v-model="company_id" filterable clearable placeholder="请选择母基金">
						<el-option v-for="item in company_list" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</div>
			</div>
			<div class="search_group_bottom flex_start">
				<div class="search_box">
					<el-select v-model="fund_type" clearable placeholder="基金类别" @change="chooseFundType">
						<el-option v-for="item in fund_type_list" :key="item.value" :label="item.label" :value="item.value">
						</el-option>
					</el-select>
				</div>
				<div class="search_box">
					<el-select v-model="fund_id" filterable clearable placeholder="请选择投资标的">
						<el-option v-for="item in fund_list" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</div>
				<div class="date_box">
					<el-date-picker v-model="start_time" type="date" placeholder="选择开始日期" value-format="yyyy-MM-dd" :clearable="false" :editable='false'>
    					</el-date-picker>
				</div>
				<div class="date_middle_text">
					至
				</div>
				<div class="date_box">
					<el-date-picker v-model="end_time" type="date" placeholder="选择截止日期" value-format="yyyy-MM-dd" :clearable="false" :editable='false'>
    					</el-date-picker>
				</div>
				<div class="btn_box">
					<button @click="getInitData(1)">搜索</button>
				</div>
			</div>
		</div>
		<div class="table_title flex_between">
			<div class="">
				母基金投资组合总数<span>(&nbsp;{{ total }}&nbsp;)</span>
			</div>
			<div class="flex_end">
				<button class="blue_btn" @click="downloadExcel">
					<span class="iconfont icon-daoru"></span>
					导出excel表格
				</button>
			</div>
		</div>
		<div class="table">
			<el-table :data="tableData" border style="width: 100%">
				<el-table-column prop="id" label="序号" width="80">
					<template slot-scope="scope">
						{{ (scope.$index + 1) + (currentPage - 1) * pageSize }}
					</template>
				</el-table-column>
				<el-table-column sortable prop="fofname" label="母基金全称" width="200">
				</el-table-column>
				<el-table-column sortable prop="id" label="ID" width="80">
				</el-table-column>
				
				<el-table-column sortable prop="number" label="编号" width="80">
				</el-table-column>
				<el-table-column sortable prop="fund_type_name" label="投资标的类别" width="180">
				</el-table-column>
				<el-table-column sortable prop="name" label="投资标的全称" width="180">
				</el-table-column>
				<el-table-column sortable prop="protocol_date" label="协议签署日" width="150">
				</el-table-column>
				<el-table-column sortable prop="invest_end_date" label="投资到期日" width="150">
				</el-table-column>
				<el-table-column sortable prop="subscribe" label="认缴（万元）" width="150">
				</el-table-column>
				<el-table-column sortable prop="multiple" label="倍数" width="110">
				</el-table-column>
				<el-table-column sortable prop="update_time" label="更新日期" width="180">
				</el-table-column>
				<el-table-column sortable prop="investment" label="投资（万元）" width="140">
				</el-table-column>
				
				<el-table-column sortable prop="back" label="回款返本（万元）" width="180">
				</el-table-column>
				<el-table-column sortable prop="income" label="回款收益-税后（万元）" width="200">
				</el-table-column>
				<el-table-column sortable prop="irr" label="XIRR" width="120">
				</el-table-column>
				<template slot="empty">
					<div class="table-nodata flex_start">
						<img src="../../images/table_nodata.png">
						<span>没有对应数据</span> 
					</div>
				</template>
			</el-table>
			<el-pagination v-if="total" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
			</el-pagination>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getInvestorInvestGroupFundSelectListApi,investListReportMotherApi,investDownloadReportMotherApi } from '../../config/api'
	
	export default {
		name: 'motherFundInvestTotal',
		data() {
			return {
				fund_type: '',
				company_id: '',
				fund_id: '',
				start_time: '',
				end_time: '',
				
				fund_list: [],
				company_list: [],
				fund_type_list: [
					{
						value: 1,
						label: '母基金'
					},
					{
						value: 2,
						label: '子基金'
					},
					{
						value: 3,
						label: '直投项目'
					},
					{
						value: 4,
						label: '自管基金'
					}
				],
				currentPage: 1,
				pageSize: 10,
				total: 0,
				
				tableData: [ ]
			}
		},
		created() {
			this.getFundSelectList(2,1)
			this.getInitData()
		},
		mounted() {},
		components: {},
		methods: {
			getInitData(is_init_page){
				// 判断结束时间是否大于开始时间
				if (this.start_time > this.end_time) {
					dialogConfirm("info", '结束时间不能小于开始时间');
					return
				}
				if (is_init_page) {
					this.currentPage = 1;
				}
				investListReportMotherApi({
					fof_id: this.company_id,
					fund_type: this.fund_type,
					fund_id: this.fund_id,
					start_time: this.start_time,
					end_time: this.end_time,
					page: this.currentPage,
					pageSize: this.pageSize
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						this.tableData = res.data.payload.data;
						this.total = res.data.payload.total;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			downloadExcel(){
				investDownloadReportMotherApi({
					fof_id: this.company_id,
					fund_type: this.fund_type,
					fund_id: this.fund_id,
					start_time: this.start_time,
					end_time: this.end_time
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload.path;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			getFundSelectList(e,is_fund_list){
				getInvestorInvestGroupFundSelectListApi({
					type: e
				}).then(res => {
					//console.log(res);
					if(res.data.msgNo == 1) {
						if (e == 2) {
							if (is_fund_list) {
								this.company_list = res.data.payload;
							} else {
								this.fund_list = res.data.payload;
							}
						} else{
							this.fund_list = res.data.payload;
						}
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			chooseFundType(e){
				this.fund_id = '';
				if (e) {
					if (e == 1) {
						e = 2;
					} else if (e == 2) {
						e = 4;
					} else if(e == 3){
						e = 5;
					} else {
						e = 3;
					}
					this.getFundSelectList(e)
				} else{
					this.fund_list = [];
				}
			},
			
			
			// 分页功能模块
			changeCurrentPage(e){
				this.currentPage = e;
				this.getInitData()
			},
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getInitData()
			},
		},
	}
</script>

<style scoped="scoped">
	.body{
		padding: 0 !important;
	}
	.search_group{
		padding: 30px 0;
    		border-bottom: 1px solid #F1F1F1;
	}
	.search_group_bottom{
		margin-top: 20px;
	}
	.search_group_bottom .search_box{
		width: 140px;
	}
	
	.search_box{
		width: 170px;
		height: 30px;
		margin-right: 13px;
	}
	.date_box{
		width: 162px;
	}
	.date_middle_text{
		margin: 0 10px;
	}
	.btn_box{
		margin-left: 24px;
	}
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	.table_title{
		margin: 26px 0;
		color: #6292F4;
		font-size: 14px;
	}
	.table_title span{
		color: #6292F4;
	}
</style>


